.nav-wrapper {
  background-color: rgba(0, 0, 0, 0.555);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  min-height: 4.5rem;
  width: 100%;
  margin: 0 auto;
  z-index: 111;
  padding: 0;
  display: block;
  transition: all 0.2s ease-in-out;
  transform-style: preserve-3d;
  backface-visibility: hidden;
  -webkit-transition: all 0.2s ease-in-out;
  -webkit-transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;

  .toggle-nav {
    background-color: transparent;
    cursor: pointer;
    box-shadow: none;
    border: 0;
    outline: none;
    margin: 0;
    padding: 0.5rem;
  }

  .logo-link {
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.5rem;
  }
  .logo-link > .logo {
    max-width: 144px;
    width: 100%;
    height: auto;
    backface-visibility: hidden;
    transform-style: preserve-3d;
    -webkit-backface-visibility: hidden;
    -webkit-transform-style: preserve-3d;
    transition: max-height 0.2s ease-in-out;
    -webkit-transition: max-height 0.2s ease-in-out;
  }

  .nav-list {
    background-color: transparent;
    list-style: none;
    margin: 0;

    padding: 0;
    display: block;
    position: absolute;
    top: 4.5rem;
    bottom: 0;
    left: -15rem;
    width: 0;
    min-height: 100vh;
    -webkit-overflow-scrolling: touch;
    transition: all 0.3s ease-in-out;
    backface-visibility: hidden;
    transform-style: preserve-3d;
    -webkit-backface-visibility: hidden;
    -webkit-transform-style: preserve-3d;
  }
  .nav-list.-open {
    width: 100%;
    left: 0;
    padding-bottom: 4.5rem;
  }
  .nav-list.-open > .list.-left {
    overflow-y: auto;
    overscroll-behavior-y: auto;
    -webkit-overflow-scrolling: touch;
  }
  .nav-list > .list.-left {
    background: #212121;
    position: relative;
    width: 60%;
    height: 100%;
  }
  .nav-list > .list.-right {
    width: 40%;
  }
  .nav-list > .list > .item {
    display: flex;
    padding: 0;
    border-bottom: 1px solid rgba(176, 176, 176, 0.5);
  }
  .nav-list > .list > .item:last-child {
    border-bottom: 0;
  }
  .nav-list > .list > .item > .link {
    border-color: transparent;
    padding: 0.75rem 1rem 0.5rem 1rem;
    flex-basis: 100%;
    display: block;
    background: transparent;
    font-size: 1.5rem;
    padding-left: 1.5rem;
    color: #e0e0e0;
    transition: all 0.3s ease-in-out;
    text-decoration: none;
  }
  .nav-list > .list > .item > .link:hover,
  .nav-list > .list > .item > .link.-active {
    color: #59afd1;
  }
  .nav-list > .list > .overlay {
    width: 40%;
    height: 100%;
    background: rgba(59, 59, 59, 0.65);
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
  }
  @media all and (min-width: 800px) {
    .nav-list {
      top: unset;
      left: unset;
      position: relative;
      width: 100%;
      height: 100%;
      min-height: initial;
      background: transparent;
      overflow: hidden;
      margin-left: 20px;
    }
    .nav-list > .list.-left,
    .nav-list > .list.-right {
      overflow: hidden;
    }
    .nav-list > .list.-left.-left,
    .nav-list > .list.-right.-left {
      height: 100%;
      width: 100%;
      border-top: 0;
      background: transparent;
      display: flex;
      align-items: center;
    }
    .nav-list > .list.-left.-right,
    .nav-list > .list.-right.-right {
      display: none;
      z-index: -1;
    }
    .nav-list > .list > .item {
      margin-left: 1.5rem;
      margin-bottom: 0;
      border-bottom-color: transparent;
      justify-content: flex-start;
    }
    .nav-list > .list > .item > .link {
      border-bottom: 2px solid transparent;
      display: block;
      flex-basis: unset;
      background: transparent;
      padding: 1.25rem 0;
      font-size: 1rem;
      outline: 0;
      transition: all 0.2s ease-in-out;
    }
  }

  ._disableScroll {
    overflow-y: hidden !important;
  }

  .burger {
    height: 3em;
    width: 3em;
    position: relative;
    font-size: 10px;
    cursor: pointer;
    -webkit-transition: 0.2s all;
    -o-transition: 0.2s all;
    transition: 0.2s all;
  }
  .burger:after {
    content: "";
    display: block;
    position: absolute;
    height: 150%;
    width: 150%;
    top: -25%;
    left: -25%;
  }
  .burger > .burger-lines {
    top: 50%;
    margin-top: -0.125em;
  }
  .burger > .burger-lines:before {
    left: 0;
    top: 1em;
  }
  .burger > .burger-lines:after {
    left: 0;
    top: -1em;
  }
  .burger.-offset > .burger-lines {
    top: 50%;
    margin-top: -0.125em;
  }
  .burger.-offset > .burger-lines:before {
    left: 1em;
    top: 1em;
  }
  .burger.-offset > .burger-lines:after {
    left: 0;
    top: -1em;
  }
  .burger.-squeeze .burger-lines,
  .burger.-squeeze .burger-lines:before,
  .burger.-squeeze .burger-lines:after {
    -webkit-transition: 0.2s top 0.2s, 0.1s left, 0.2s transform,
      0.4s background-color 0.2s;
    -o-transition: 0.2s top 0.2s, 0.1s left, 0.2s transform,
      0.4s background-color 0.2s;
    transition: 0.2s top 0.2s, 0.1s left, 0.2s transform,
      0.4s background-color 0.2s;
  }
  .burger.-squeeze .burger-lines:after {
    left: 0;
    top: -1em;
  }
  .burger.-squeeze .burger-lines:before {
    left: 0;
    top: 1em;
  }
  .burger.-squeeze.-offset .burger-lines:before,
  .burger.-squeeze.-offset .burger-lines:after {
    width: 2em;
  }
  .burger.-squeeze.-offset .burger-lines:after {
    left: 0;
    top: -1em;
  }
  .burger.-squeeze.-offset .burger-lines:before {
    left: 1em;
    top: 1em;
  }
  .burger.-squeeze.open .burger-lines,
  .burger.-squeeze.open .burger-lines:before,
  .burger.-squeeze.open .burger-lines:after {
    -webkit-transition: 0.2s background-color, 0.2s top, 0.2s left,
      0.2s transform 0.15s;
    -o-transition: 0.2s background-color, 0.2s top, 0.2s left,
      0.2s transform 0.15s;
    transition: 0.2s background-color, 0.2s top, 0.2s left, 0.2s transform 0.15s;
  }
  .burger.-squeeze.open .burger-lines {
    background-color: transparent;
  }
  .burger.-squeeze.open .burger-lines:before,
  .burger.-squeeze.open .burger-lines:after {
    left: 0.5em;
    top: 0px;
  }
  .burger.-squeeze.open .burger-lines:before {
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    transform: rotate(-45deg);
  }
  .burger.-squeeze.open .burger-lines:after {
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }

  .burger-lines,
  .burger-lines:after,
  .burger-lines:before {
    pointer-events: none;
    display: block;
    content: "";
    width: 100%;
    border-radius: 0.25em;
    background-color: #e0e0e0;
    height: 0.25em;
    position: absolute;
    -webkit-transform: rotate(0);
    -ms-transform: rotate(0);
    transform: rotate(0);
  }

  .burger.-squeeze > .burger-lines {
    top: 50%;
    margin-top: -0.125em;
  }
  .burger.-squeeze > .burger-lines,
  .burger.-squeeze > .burger-lines:after,
  .burger.-squeeze > .burger-lines:before {
    transition: 0.2s top 0.2s, 0.1s left, 0.2s transform,
      0.4s background-color 0.2s;
  }
  .burger.-squeeze > .burger-lines:after {
    left: 0;
    top: -1em;
  }
  .burger.-squeeze > .burger-lines:before {
    left: 0;
    top: 1em;
  }

  .burger.-squeeze.-offset > .burger-lines,
  .burger.-squeeze.-offset > .burger-lines:after,
  .burger.-squeeze.-offset > .burger-lines:before {
    transition: 0.2s top 0.2s, 0.1s left, 0.2s transform,
      0.4s background-color 0.2s;
  }

  .burger.-squeeze.-offset > .burger-lines:after,
  .burger.-squeeze.-offset > .burger-lines:before {
    width: 2em;
  }

  .burger.-squeeze.-offset > .burger-lines:after {
    left: 0;
    top: -1em;
  }

  .burger.-squeeze.-offset > .burger-lines:before {
    left: 1em;
    top: 1em;
  }

  .burger.-squeeze.open > .burger-lines,
  .burger.-squeeze.open > .burger-lines:after,
  .burger.-squeeze.open > .burger-lines:before {
    transition: 0.2s background-color, 0.2s top, 0.2s left, 0.2s transform 0.15s;
  }

  .burger.-squeeze.open > .burger-lines:after,
  .burger.-squeeze.open > .burger-lines:before {
    width: 2em;
  }

  .burger.-squeeze.open > .burger-lines {
    background-color: transparent;
  }

  .burger.-squeeze.open > .burger-lines:before,
  .burger.-squeeze.open > .burger-lines:after {
    left: 0.5em;
    top: 0px;
  }

  .burger.-squeeze.open > .burger-lines:before {
    transform: rotate(-45deg);
  }

  .burger.-squeeze.open > .burger-lines:after {
    transform: rotate(45deg);
  }
}
.nav-wrapper > .nav {
  max-width: 80rem;
  width: 100%;
  height: 5.5rem;
  margin: 0 auto;
  padding: 0 0.5rem;
  display: flex;
  justify-content: space-between;
}
.nav-wrapper > .nav > .toggle-nav {
  z-index: 2;
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
}
@media all and (min-width: 800px) {
  .nav-wrapper > .nav > .toggle-nav {
    display: none;
  }
}
.nav-wrapper.-sticky {
  position: fixed;
}
.nav-wrapper.-sticky.-bottom {
  top: unset;
  bottom: 0;
}
.nav-wrapper.-sticky.-bottom > .nav > .nav-list {
  bottom: 4.5rem;
  top: 0;
  transform: translateY(calc(4.5rem - 100%));
}
.nav-wrapper.-sticky.-bottom > .nav > .nav-list > .list.-left {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
@media screen and (min-width: 800px) {
  .nav-wrapper.-sticky.-bottom > .nav > .nav-list > .list.-left {
    flex-direction: row;
    justify-content: flex-start;
  }
}
@media screen and (min-width: 800px) {
  .nav-wrapper.-sticky.-bottom {
    top: unset;
    bottom: unset;
  }
}
